/* ==========================================================================
  Typography
========================================================================== */
body {
  font-family: 'FazzNeue', sans-serif;
  font-size: 14px;
  line-height: 1.3;
  color: #333333;
}

h1 { font-size: 36px;}
h2 { font-size: 28px; }
h3 { font-size: 20px; }
h4 { font-size: 16px; }
h5 { font-size: 12px; }
h6 { font-size: 10px; }

h1, h2, h3, h4, h5, h6 {
  margin: 0.25em 0 0.55em 0;
  font-weight: bold;
  line-height: 1.4;
}

p { margin: 0; }
p + p { margin: 0 0 20px 0; }

a,
a:hover,
a:active {
  text-decoration: none;
  transition: 0.3s;
  color: inherit;
}

img {
  vertical-align: middle;
  max-width: 100%;
}

table {
  width: 100%;
  border-collapse: collapse;
}

table + table {
  margin-top: 30px;
}

.tur-table-main {
  border: 1px solid #eeeeee;
}

.tur-table-main td,
.tur-table-main th {
  padding: 15px;
  border: 0;
}

th {
  background-color: #eeeeee;
}

td, th {
  padding: 15px 0;
  border-bottom: 1px solid #eeeeee;
}



.tur-row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-right: -8px;
  margin-left: -8px;
}

.tur-col {
  -ms-flex-preferred-size: 0;
  flex-basis: 0;
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  max-width: 100%;
  padding: 0 8px;
}

.tur-col-25 {
  width: 25%;
}

.tur-wrap-title {
  padding-bottom: 30px;
}

.tur-wrap-title h1 {
  color: #0869ff;
}